
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { WebServiceConfiguration } from '../utilities/webServiceConfiguration';

@Injectable({
  providedIn: 'root'
})
export class VideoService extends WebServiceConfiguration {

  constructor(private http: HttpClient) {
    super();
  }

  public getVideosDefault(){
    const url = this.beginningLinkYoutubeAcces + 'search?key=AIzaSyCwxpkHS0Hl6ygZk6Whz4eIf1JZHNNiVLk&part=snippet,id&order=date&maxResults=6';
    console.log(url);
    this.http.get(url).subscribe(response => console.log(response));
}
}
